import React, {useContext, useEffect} from "react";
import { Container } from "../components/container";
import { Header } from "../components/header";
import { css } from "emotion";
import { UserContext } from "../app";
import { UploadAvatar } from "../components/upload-avatar";
import { CustomForm } from "../components/custom-form";
import { IChangePassword, IUser } from "../entity";
import { TextField } from "../components/text-field";
import { Button, Card, Typography } from "@material-ui/core";
import { isEqual, omit } from "lodash";
import {useCustomSnackbar, usePost, useUser} from "../hooks";
import { equalTo, getServerError } from "../utils";
import * as Yup from "yup";
import {UserPostsTable} from "../widgets/user-posts-table";

const styles = {
    content: css`
        padding: 40px 0;
        display: grid;
        grid-template-columns: 300px 1fr 1fr;
        grid-column-gap: 40px;
        align-items: flex-start;
    `,
    avatar: css`
        padding: 20px;
    `,
    image: css`
        height: 300px;
    `,
    form: css`
        display: grid;
        grid-template-columns: 1fr;
        grid-row-gap: 30px;
        padding: 20px;
    `,
};

const changeUserDataValidationSchema = Yup.object().shape({
    email: Yup.string().email(" e-mail").required("  "),
    login: Yup.string()
        .required("  ")
        .min(4, "     4 "),
});

Yup.addMethod(Yup.string, "equalTo", equalTo);

const changePasswordValidationSchema = Yup.object().shape({
    password: Yup.string()
        .min(6, "    6 ")
        .required("   "),
    newPassword: Yup.string()
        .equalTo(Yup.ref("repeatPassword"), "      ")
        .min(6, "    6 ")
        .required("   "),
    repeatPassword: Yup.string()
        .equalTo(Yup.ref("newPassword"), "      ")
        .min(6, "    6 ")
        .required("   "),
});

export const Profile = () => {
    const userContext = useContext(UserContext);
    const { uploadAvatar, updateUser, deleteAvatar, changePassword, userPosts, getUserPosts } = useUser();
    const { showErrorSnackbar, showSuccessSnackbar } = useCustomSnackbar();
    const { deletePost } = usePost();

    useEffect(() => {
        getUserPosts();
    }, []);

    const handleAvatar = (file: File) => {
        uploadAvatar({ avatar: file })
            .then((response) => {
                userContext.setUser(response.data);
                showSuccessSnackbar(" ");
            })
            .catch((err) => {
                const error = getServerError(err);
                if (error) {
                    showErrorSnackbar(error.title);
                }
            });
    };

    const handleDeleteAvatar = () => {
        deleteAvatar()
            .then((response) => {
                userContext.setUser(response.data);
                showSuccessSnackbar(" ");
            })
            .catch((err) => {
                const error = getServerError(err);
                if (error) {
                    showErrorSnackbar(error.title);
                }
            });
    };

    const handleUpdateUser = (data: Partial<IUser>) => {
        updateUser(omit(data, ["avatar"]))
            .then((response) => {
                userContext.setUser(response.data);
                showSuccessSnackbar(" ");
            })
            .catch((err) => {
                const error = getServerError(err);
                if (error) {
                    showErrorSnackbar(error.title);
                }
            });
    };

    const handleChangePassword = (data: IChangePassword) => {
        changePassword(data)
            .then(() => showSuccessSnackbar(" "))
            .catch((err) => {
                const error = getServerError(err);
                if (error) {
                    showErrorSnackbar(error.title);
                }
            });
    };

    const onDeletePost = (id: number) => {
        return deletePost(id).then(getUserPosts);
    };

    return (
        <>
            <Header />
            <Container>
                <Typography
                    variant={"h3"}
                    className={css`
                        margin-top: 40px !important;
                    `}
                >
                      {userContext.user.login}
                </Typography>
                <div className={styles.content}>
                    <CustomForm<Partial<IUser>>
                        data={userContext.user}
                        validationSchema={changeUserDataValidationSchema}
                        onSubmit={handleUpdateUser}
                        render={(form) => (
                            <>
                                <Card className={styles.avatar} variant={"outlined"}>
                                    <UploadAvatar
                                        src={userContext.user.avatar}
                                        onDeleteAvatar={handleDeleteAvatar}
                                        uploadAvatar={handleAvatar}
                                        imageClassName={styles.image}
                                        name={"avatar"}
                                    />
                                </Card>
                                <Card className={styles.form} variant={"outlined"}>
                                    <Typography variant={"h5"}> </Typography>
                                    <TextField
                                        name={"email"}
                                        label={"Email"}
                                        InputLabelProps={{ shrink: !!form?.values?.login }}
                                    />
                                    <TextField
                                        name={"login"}
                                        label={""}
                                        InputLabelProps={{ shrink: !!form?.values?.login }}
                                    />
                                    <Button
                                        fullWidth
                                        variant="contained"
                                        color="primary"
                                        onClick={form?.submitForm}
                                        disabled={
                                            !form.isValid || isEqual(form?.values, form?.initialValues)
                                        }
                                    >
                                        
                                    </Button>
                                </Card>
                            </>
                        )}
                    />
                    <CustomForm<IChangePassword>
                        onSubmit={handleChangePassword}
                        validationSchema={changePasswordValidationSchema}
                        render={(form) => (
                            <Card variant={"outlined"} className={styles.form}>
                                <Typography variant={"h5"}> </Typography>
                                <TextField name={"password"} label={" "} />
                                <TextField name={"newPassword"} label={" "} />
                                <TextField name={"repeatPassword"} label={" "} />
                                <Button
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    onClick={form?.submitForm}
                                    disabled={
                                        !form.isValid || isEqual(form?.values, form?.initialValues)
                                    }
                                >
                                    
                                </Button>
                            </Card>
                        )}
                    />
                </div>
                <Typography variant={"h5"}> </Typography>
                <UserPostsTable posts={userPosts} onDelete={onDeletePost} />
            </Container>
        </>
    );
};
